import React, { useState } from 'react'
import DropdownButton from 'react-bootstrap/DropdownButton'
import Dropdown from 'react-bootstrap/Dropdown'

export  function TimeCondition(props) {
    const [timeRangeTog, settimeRangeTog] = useState(false)
    return (
        <div>
            
        <span>Run task  {timeRangeTog ?
            <span><button onClick={()=>{settimeRangeTog(false)}}>At</button> <input type='time'></input></span> :
            <span><button onClick={()=>{settimeRangeTog(true)}}>Between</button> <input type='time'></input> and <input type='time'></input></span>
        } on <DropdownButton id="dropdown-basic-button" title="days">
             <Dropdown.Item>Sunday</Dropdown.Item>
             <Dropdown.Item>Monday</Dropdown.Item>
             <Dropdown.Item>Tuesday</Dropdown.Item>
             <Dropdown.Item>Wednesday</Dropdown.Item>
             <Dropdown.Item>Thursday</Dropdown.Item>
             <Dropdown.Item>Friday</Dropdown.Item>
             <Dropdown.Item>Saturday</Dropdown.Item>
            </DropdownButton><button onClick={()=>{props.deleteFunc(props.index,props.type)}}>X</button></span>
            
        </div>
    )
}

export function GeolocationCondition(props) {
    return (
        <div>
            <span>Longitude: <input type='number'></input> Latitude: <input type='number'></input> Radius(M): <input type='number'></input></span>
            <button onClick={()=>{props.deleteFunc(props.index,props.type)}}>X</button>
        </div>
    )
}

export function AndOr(props) {
    return (
        <div>
            <button onClick={()=>{props.triggerValue(props.index,props.value,props.arrayType)}}>{props.value}</button>
            
        </div>
    )
}

export function PushNotification(props) {
    return (
        <div>
            <input type='text' placeholder='Your notification text'></input>
            <button onClick={()=>{props.deleteFunc(props.index,props.type)}}>X</button>
        </div>
    )
}